html,
body {
  font-family: "Roboto Condensed", serif;
  margin: 0;
  padding: 0;
  -webkit-text-size-adjust: none;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
}

b {
  font-family: "Roboto", serif;
  font-size: 26px;
}

p {
  margin: 8px 0;
}

@media all and (max-width: 800px) {
  b {
    font-size: 20px;
  }
}

@import url("https://fonts.googleapis.com/css?family=Roboto+Condensed|Roboto");
